@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;900&family=Libre+Caslon+Text&display=swap');

:root {
    --navbar-height: 100px;
}

body {
    background: #fefefe;
    height: 3000px;
    font-family: 'Libre Caslon Text', serif;
    border: 0;
    padding: 0;
    margin: 0;
    color: #03224e;
}

.site-container {
    max-width: 1920px;
    margin: auto;
}

.navbar {
    height: var(--navbar-height);
    width: 100vw;
    position: fixed;
    top: 0;
    display: flex;
    justify-content: space-between;
    align-content: center;
    background: #fefefe;
    z-index: 1;
}

.logo {
    margin: 20px 10px;
}

.menu-container {
    position: relative;
}

.hamburger {
    margin: 37px 25px;
    cursor: pointer;
}

.hamburger div {
    width: 35px;
    height: 0;
    border: 3px solid black;
    margin: 2px;
}

.about-us {
    margin-top: var(--navbar-height);
    background-image: url("img/hero-image.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
    height: calc(100vh - var(--navbar-height));
    max-height: 800px;
    width: min(100vw, 1920px);
    max-width: 100%;
    box-sizing: border-box;
}

.orange-header {
    color: #bf6050;
    text-transform: uppercase;
    font-size: 1.3em;
}

.section h1 {
    font-size: 2.3em;
}

.divider {
    background-color: #bf6050;
    height: 2px;
    margin: auto;
}

.divider-large {
    max-width: 750px;
}

.divider-small {
    max-width: 200px;
}

.section h1,
.orange-header {
    font-family: 'Lato', sans-serif;
    font-weight: 900;
    letter-spacing: 2px;
}

.section p {
    margin-top: 50px;
    max-width: 80vw;
    text-align: center;
    display: block;
    margin: 50px auto;
    font-size: 1.5em;
    line-height: 1.5em;
}

#hero-caption {
    max-width: 600px;
    margin-top: 30px;
}

.button {
    background-color: #da6f5c;
    border: 0;
    font-family: 'Lato', sans-serif;
    font-weight: 900;
    letter-spacing: 2px;
    color: #fff;
    width: 250px;
    height: 75px;
    font-size: 1.4em;
    border-radius: 7px;
}

.button-contact {
    margin: 25px 0;
}

.section {
    text-align: center;
    padding: 60px 35px;
}

.light {
    background: #f1f1f1;
}

.dark {
    background: #292728;
    color: #fefefe;
}

.image-section {
    display: flex;
    flex-direction: row;
}

.image-section.odd {
    flex-direction: row;
}

.image-section.even {
    flex-direction: row-reverse;
}

.image-section > * {
    width: 50vw;
    height: 50vw;
    max-width: 1100px;
    max-height: 1100px;
}

.image-1 {
    background: url('img/foto1.jpg') center no-repeat;
    background-size: cover;
}

.image-2 {
    background: url('img/foto2.jpg') center no-repeat;
    background-size: cover;
}

.image-3 {
    background: url('img/foto3.jpg') center no-repeat;
    background-size: cover;
}

.image-4 {
    background: url('img/foto4.jpg') center no-repeat;
    background-size: cover;
}

.image-5 {
    background: url('img/foto5.jpg') center no-repeat;
    background-size: cover;
}

.description {
    text-align: center;
    position: relative;
}

.description-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);
    padding: 0 35px;
}

.description h3 {
    font-family: 'Lato', sans-serif;
    font-weight: 900;
    letter-spacing: 2px;
    font-size: 2em;
    margin-bottom: 10px;
}

.description .divider {
    margin-bottom: 15px;
}

.description p {
    width: 25vw;
    max-width: 850px;
    margin: auto;
    font-size: 1.5em;
}

.icons {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.icon-item {
    color: inherit;
    text-decoration: none;
    font-family: 'Lato', sans-serif;
    letter-spacing: 2px;
    font-size: 1.3em;
    display: block;
    width: 30vw;
    margin: 30px;
}

.icon {
    width: 80px;
    display: block;
    margin: 30px auto; 
}

.icon-item span {
    display: block;
}

@media (max-width: 1250px) {
    .description p {
        font-size: 1.2em;
    }
}

@media (max-width: 1050px) {
    .image-section.odd,
    .image-section.even {
        flex-direction: column;
    }
    
    .image-section > * {
        width: 100vw;
        height: 100vw;
        max-width: 100%;
    }

    .description p {
        width: 80vw;
        margin: auto;
    }

    .icons {
        flex-direction: column;
        align-items: center;
    }

    .icon-item {
        width: 90vw;
    }
}
